.single-hero-slide {
  background-color: #ffffff;
  padding-top: 20px;
  padding-bottom: 220px;
  font-family: "Josefin Sans", sans-serif;
}

.hero-slide-left {
  padding-right: 60px;
}

.hero-slide-left h2 {
  font-size: 36px;
  color: #2d2d2b;
}

.hero-slide-left h1 {
  font-size: 62px;
  line-height: 75px;
  margin-bottom: 20px;
  color: #2d2d2b;
}

.hero-slide-left p {
  color: #5c5c5c !important;
  font-family: "Roboto Slab", serif;
}

.hero-slide-left button {
  margin-top: 40px;
  font-weight: 700;
  outline: none !important;
  cursor: pointer;
  font-family: "Roboto Slab", serif;
}

.heroTeeth {
  max-width: 100%;
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.theme-btn {
  display: inline-block;
  border: 1px solid #fc5185;
  height: 61px;
  text-align: center;
  line-height: 55px !important;
  padding: 0 55px;
  background: #fc5185;
  text-transform: capitalize;
  position: relative;
  z-index: 2;
  border-radius: 3px;
  color: #2d2d2b;
  transition: 0.4s;
  font-weight: 600;
}

.btn-fill {
  margin-right: 20px;
}

.btn-fill:hover {
  background: #fff;
  color: #000 !important;
  border-color: #fff;
}

.bth-blank {
  border-color: #fff;
  background: transparent;
}

.bth-blank:hover {
  border-color: #fc5185;
  color: #2d2d2b !important;
  background: #fc5185;
}

.animate-img {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 22;
}

.animate-img .aimg1 {
  left: 210px;
  position: relative;
  animation: movebounce 2s ease-in-out infinite;
  animation-delay: 100ms;
  animation-direction: alternate;
  bottom: 15px;
}

.animate-img .aimg2 {
  position: relative;
  left: 265px;
  top: 30px;
  animation-delay: 200ms;
  animation: movebounce 2.5s ease-in-out infinite;
  animation-direction: alternate;
}

@keyframes movebounce {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0px);
  }
}

/* for mobile responsive */
