@import url("https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&family=Lora:ital,wght@0,400;0,500;0,600;1,400&family=Roboto+Slab:wght@300;400;500;600;700&display=swap");

.container-head {
  border-radius: 10px;
  transition: all 0.5s ease-in-out;
  padding: 0 10px;
}

.head-bg {
  background-color: #fff;
}

.list-item {
  padding: 25px 0;
  display: inline-block;
  color: #1c83c6;
  text-transform: capitalize;
  padding-left: 40px;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 18px;
  font-weight: 400;
  font-family: "Roboto Slab", serif;
}

.list-item:hover {
  color: #2d2d2b;
}

.ml-5 {
  margin-left: 20px !important;
}

.ml-5:hover {
  border-color: #ffff !important;
}

.userName {
  color: #004caa;
  margin-left: 5px;
}

.btn {
  margin-left: 15px;
  margin-right: 15px;
}

.btn-danger {
  font-family: Raleway-SemiBold;
  font-size: 18px;
  font-weight: bold;
  color: #2d2d2b;
  letter-spacing: 1px;
  line-height: 15px;
  border: 2px solid #2d2d2b;
  border-radius: 40px;
  background: transparent;
  transition: all 0.3s ease 0s;
}

.btn-danger:hover {
  color: #fff;
  background: #fff;
  border: 2px solid #fff;
}

/* for mobile responsive */

@media (max-width: 992px) {
  /* 2: Navbar */
  .btn {
    margin-left: 57px;
    margin-right: 15px;
    margin-bottom: 20px;
  }
}

@media (max-width: 426px) {
  .list-item {
    padding: 15px 0;
  }

  .btn {
    margin: 15px auto;
  }
}
