.dentist-details-sec {
  padding: 120px 0;
}

.single-dentist-details h2 {
  font-size: 36px;
  line-height: 48px;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 600;
}

.single-dentist-details h2 span {
  font-size: 20px;
  color: #666;
  font-weight: 500;
}

.single-dentist-details p {
  font-weight: 300;
  font-size: 16px;
  color: #666;
  text-align: justify;
}

.dentist-award {
  margin-top: 40px;
}

.dentist-award h3 {
  margin-bottom: 7px;
  font-size: 24px;
  line-height: 36px;
  font-family: "Josefin Sans", sans-serif;
  color: black;
}

.dentist-award ul li {
  position: relative;
  display: flex;
  padding-left: 16px;
  margin-right: 25px;
  margin-top: 14px;
}

.dentist-award ul li::before {
  position: absolute;
  left: 0;
  border-radius: 50%;
  background-color: #1c83c6;
  width: 9px;
  height: 9px;
  content: "";
  top: 11px;
}

.profile-img {
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  margin-bottom: 35px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 8.135px 18.271px 40px 0 rgb(0 0 0 / 8%);
  height: 350px;
  overflow: hidden;
}

.achivement-img-bg {
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  border-radius: 10px;
  min-height: 405px;
  overflow: hidden;
}

.price__tabs__tab__content__item {
  background-color: #f3f7f8;
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.price__tabs__tab__content__price {
}
/* for mobile responsive */
@media only screen and (max-width: 426px) {
  .achivement-img-bg {
    min-height: 225px;
  }
}

@media only screen and (max-width: 376px) {
  .achivement-img-bg {
    min-height: 200px;
  }
}

@media only screen and (max-width: 321px) {
  .achivement-img-bg {
    min-height: 175px;
  }
}
